<template>
  <div>
    <select 
      v-model="selectedDataRowItemOption"
      @change="setDataRowItemOption"
      class="ds-form-select data-row-options"
    >
      <option value="1_yes/no">yes/no</option>
      <option value="2_Yes/No">Yes/No</option>
      <option value="3_true/false">true/false</option>
      <option value="4_True/False">True/False</option>
    </select>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number
  },
  setup(props) {
    const store = useStore();
    // default value
    let selectedDataRowItemOption = ref("1_yes/no");

    // dafault dataRowItemOption
    let selectedDataRowItemOptionSplit = ref(computed(() => selectedDataRowItemOption.value.split("_")));
    let dataRowItemOption = ref({
      typeIndex: Number(selectedDataRowItemOptionSplit.value[0]),
      typeName: selectedDataRowItemOptionSplit.value[1]
    });
    
    // 선택한 data type을 store에 저장
    const setDataRowItemOption = (() => {
      console.log("dataRowItemOption -->", store.getters.getDataRowItemOption(props.index));
      store.commit("setDataRowItemOption", {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value
      });
    });

    return {
      selectedDataRowItemOption,
      selectedDataRowItemOptionSplit,
      dataRowItemOption,
      setDataRowItemOption
    }
  },
});
</script>

<style lang="scss" scoped>
.data-row-options {
  appearance: auto !important;
  width: auto;
}
</style>
